}

.hover\:text-gray-200:hover {
	color: #e5e7eb;
}

.hover\:bg-opacity-30:hover {
	background-color: rgba(31, 41, 55, 0.3);
}

.hover\:scale-105:hover {
	transform: scale(1.05);
}

.hover\:scale-110:hover {
